// stories/o-button-group.stories.ts
import type { Meta, StoryObj } from '@storybook/web-components';
import { html } from 'lit';
import type { OButtonGroup } from '../components/o-button-group/o-button-group';

export default {
	title: 'Components/Button Group'
} as Meta;

const Template: StoryObj<OButtonGroup> = {
	parameters: {
		layout: 'centered'
	},
	render: () => html`
		<o-button-group>
			<o-button variant="secondary">First Button</o-button>
			<o-button variant="secondary">Inner Button</o-button>
			<o-button variant="secondary">Last Button</o-button>
		</o-button-group>
	`
};

const TemplateMultiStyles: StoryObj<OButtonGroup> = {
	parameters: {
		layout: 'centered'
	},
	render: () => html`
		<o-button-group>
			<o-button variant="secondary">First Button</o-button>
			<o-button variant="primary">Last Button</o-button>
		</o-button-group>
	`
};

const TemplateDisabled: StoryObj<OButtonGroup> = {
	parameters: {
		layout: 'centered'
	},
	render: () => html`
		<o-button-group>
			<o-button variant="secondary">First Button</o-button>
			<o-button variant="secondary" disabled>Inner Button</o-button>
			<o-button variant="secondary" disabled>Last Button</o-button>
		</o-button-group>
	`
};

export const Default = {
	...Template,
	parameters: {
		docs: {
			source: {
				transform: () => {
					return `
-----------------------
// Vue Code Snippet
-----------------------

import { OButtonGroup } from "@oui/vue";
  
<o-button-group>
  <o-button variant="secondary">First Button</o-button>
  <o-button variant="secondary">Inner Button</o-button>
  <o-button variant="secondary">Last Button</o-button>
</o-button-group>

-----------------------
// React Code Snippet
-----------------------
  
import { OButtonGroup } from "@oui/react";

<OButtonGroup>
  <OButton variant="secondary">First Button</OButton>
  <OButton variant="secondary">Inner Button</OButton>
  <OButton variant="secondary">Last Button</OButton>
</OButtonGroup>
          `;
				}
			},
			description: {
				story: 'A default button group with three buttons.'
			}
		}
	}
};

export const MultiStylesButtons = {
	...TemplateMultiStyles,
	parameters: {
		docs: {
			source: {
				transform: () => {
					return `
  -----------------------
  // Vue Code Snippet
  -----------------------

  import { OButtonGroup } from "@oui/vue";
    
  <o-button-group>
    <o-button variant="secondary">First Button</o-button>
    <o-button variant="primary">Last Button</o-button>
  </o-button-group>
  
  -----------------------
  // React Code Snippet
  -----------------------
    
  import { OButtonGroup } from "@oui/react";
  
  <OButtonGroup>
    <OButton variant="secondary">First Button</OButton>
    <OButton variant="primary">Last Button</OButton>
  </OButtonGroup>
            `;
				}
			},
			description: {
				story: 'A button group where the second buttons use primary variant.'
			}
		}
	}
};

export const DisabledButtons = {
	...TemplateDisabled,
	parameters: {
		docs: {
			source: {
				transform: () => {
					return `
-----------------------
// Vue Code Snippet
-----------------------

import { OButtonGroup } from "@oui/vue";

<o-button-group>
  <o-button variant="secondary">First Button</o-button>
  <o-button variant="secondary" disabled>Inner Button</o-button>
  <o-button variant="secondary" disabled>Last Button</o-button>
</o-button-group>

-----------------------
// React Code Snippet
-----------------------
  
import { OButtonGroup } from "@oui/react";

<OButtonGroup>
  <OButton variant="secondary">First Button</OButton>
  <OButton variant="secondary" disabled>Inner Button</OButton>
  <OButton variant="secondary" disabled>Last Button</OButton>
</OButtonGroup>
          `;
				}
			},
			description: {
				story: 'A button group where the second and third buttons are disabled.'
			}
		}
	}
};
